@import "../../pages/base.scss";
.locationCard {
  display: flex;
  justify-content: space-between;
  color: $PimaryColor;
  margin-top: 0px;
  .title {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  .location,
  .name {
    font-size: 0.75rem;
  }
  .tags {
    display: flex;
    margin-top: 20px;
  }
  .tag {
    margin-right: 10px;
    background-color: $SecondaryBackground;
    color: white;
    width: 115px;
    height: 25px;
    border-radius: 10px;
    font-size: 0.5rem;
    line-height: 25px;
    text-align: center;
  }
  .picture {
    margin-left: 10px;
    width: 64px;
    border-radius: 50%;
    object-fit: cover;
  }
  .host {
    display: flex;
    align-items: center;
  }
  .profil {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }
  .name {
    text-align: end;
  }
}

@media screen and (max-width: $breakTablet) {
  .locationCard{
    flex-direction: column;
  
    margin-top: 0px;
    .title {
      font-size: 1.1rem;
    }
    .location {
      font-size: 0.6rem;
    }
    .tag {
      font-size: 0.4rem;
      width: 84px;
      height: 18px;
      line-height: 18px;
    }
    .name {
      font-size: 0.6rem;
    }
    .picture {
      width: 45px;
    }
    .profil {
      margin-top: 1rem;
      flex-direction: row-reverse;
      align-items: center;

    }
    .rating svg {
      width: 22px;
      height: 22px;
    }
  }
  .collapseHouseBloc {
    flex-direction: column;
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

@media screen and (max-width: $breakPhone) {
  .locationCard {
  
    margin-top: -50px;
    .title {
      font-size: 0.75rem;
    }
    .picture {
      width: 32px;
    }
    .name {
      font-size: 0.5rem;
    }
    .rating svg {
      width: 15px;
      height: 15px;
    }
  }
}
